				data: {
					// city:%E6%9D%AD%E5%B7%9E&key=0d1d3bf249fe023b0765942dad45b069
					city: this.city,
					key: '0d1d3bf249fe023b0765942dad45b069'
				},
				success: (resp) => {
					// this.title = '成功';
					var d=new Date();
					var hours=d.getHours();
					this.nowTemperature = resp.data.result.realtime.temperature;
					this.info = resp.data.result.realtime.info;
					if(hours>=6&&hours<18){
						this.widIcon='../../static/weather_icon/d'+resp.data.result.realtime.wid+'.gif';
					}else{
						this.widIcon='../../static/weather_icon/n'+resp.data.result.realtime.wid+'.gif';
					}
					this.temperature = resp.data.result.future[0].temperature;
					for (var i = 0; i < 5; i++) {
						// this.future[i].wid = resp.data.result.future[i].wid;
						this.future[i].temperature = resp.data.result.future[i].temperature;
						this.future[i].widIcon.icon1='../../static/weather_icon/d'+resp.data.result.future[i].wid.day+'.gif';
						this.future[i].widIcon.icon2='../../static/weather_icon/n'+resp.data.result.future[i].wid.night+'.gif';
						console.log(this.future[i].widIcon.icon1);
						for(var j=0;j<this.widList.length;j++){
							if(resp.data.result.future[i].wid.day==this.widList[j].wid){
								this.future[i].wid.wid1=this.widList[j].weather;
							}
							if(resp.data.result.future[i].wid.night==this.widList[j].wid){
								this.future[i].wid.wid2=this.widList[j].weather;
							}
								
						}
					}
				}
			})
		},
		goList(){
